import { useState, useEffect, useCallback, memo } from 'react'
import { useFilter } from '../hooks/useFilter'
import { usePagination } from '../hooks/usePagination'
import apiService from '../services/apiService'
import LoadingSpinner from './LoadingSpinner'
import Pagination from './Pagination'
import { logger } from '../utils/logger'
import '../styles/Listado.css'

const FILTER_CONFIG = {
  id: { type: 'exact' },
  telefono: { type: 'contains' },
  email: { type: 'contains' }
}

const ITEMS_PER_PAGE = 10

const TableCell = memo(({ children, className = '', title }) => (
  <td className={`table-cell ${className}`} title={title}>
    {children}
  </td>
))

TableCell.displayName = 'TableCell'

const FilterInput = memo(({ field, label, value, onChange, placeholder }) => (
  <div className="filter-group">
    <label htmlFor={`filter-${field}`} className="filter-label">
      {label}
    </label>
    <input
      id={`filter-${field}`}
      type="text"
      value={value || ''}
      onChange={(e) => onChange(field, e.target.value)}
      placeholder={placeholder}
      className="filter-input"
    />
  </div>
))

FilterInput.displayName = 'FilterInput'

const Listado = () => {
  const [users, setUsers] = useState([])
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)

  const { filteredData, filters, setFilter, clearAllFilters, hasActiveFilters } = useFilter(users, FILTER_CONFIG)

  const {
    currentPage,
    totalPages,
    totalItems,
    paginatedData,
    hasNextPage,
    hasPrevPage,
    goToPage,
    goToNextPage,
    goToPrevPage,
    goToFirstPage,
    goToLastPage,
    reset: resetPagination
  } = usePagination(filteredData, ITEMS_PER_PAGE)

  const loadUsers = useCallback(async () => {
    try {
      setLoading(true)
      setError(null)
      logger.info('Loading users...')

      const response = await apiService.getUsers()
      setUsers(response.data || [])

      logger.info(`Loaded ${response.data?.length || 0} users`)
    } catch (err) {
      logger.error('Failed to load users', err)
      setError(err.message || 'Error al cargar los usuarios')
    } finally {
      setLoading(false)
    }
  }, [])

  useEffect(() => {
    loadUsers()
  }, [])

  useEffect(() => {
    resetPagination()
  }, [filteredData.length, resetPagination])

  const handleFilterChange = useCallback((field, value) => {
    setFilter(field, value)
  }, [setFilter])

  const handleClearFilters = useCallback(() => {
    clearAllFilters()
  }, [clearAllFilters])

  const renderFilters = () => (
    <section className="filters-section" aria-label="Filtros de b√∫squeda">
      <div className="filters-header">
        <h3 className="filters-title">Filtros de B√∫squeda</h3>
        {hasActiveFilters && (
          <button
            onClick={handleClearFilters}
            className="clear-filters-button"
            type="button"
            aria-label="Limpiar todos los filtros"
          >
            Limpiar Filtros
          </button>
        )}
      </div>

      <div className="filters-grid">
        <FilterInput
          field="id"
          label="ID"
          value={filters.id}
          onChange={handleFilterChange}
          placeholder="Buscar por ID..."
        />
        <FilterInput
          field="usuario"
          label="Usuario"
          value={filters.usuario}
          onChange={handleFilterChange}
          placeholder="Buscar por usuario..."
        />
        <FilterInput
          field="nombre"
          label="Nombre"
          value={filters.nombre}
          onChange={handleFilterChange}
          placeholder="Buscar por nombre..."
        />
        <FilterInput
          field="apellido"
          label="Apellido"
          value={filters.apellido}
          onChange={handleFilterChange}
          placeholder="Buscar por apellido..."
        />
        <FilterInput
          field="email"
          label="Email"
          value={filters.email}
          onChange={handleFilterChange}
          placeholder="Buscar por email..."
        />
        <FilterInput
          field="ciudad"
          label="Ciudad"
          value={filters.ciudad}
          onChange={handleFilterChange}
          placeholder="Buscar por ciudad..."
        />
        <FilterInput
          field="especialidad"
          label="Especialidad"
          value={filters.especialidad}
          onChange={handleFilterChange}
          placeholder="Buscar por especialidad..."
        />
        <FilterInput
          field="telefono"
          label="Tel√©fono"
          value={filters.telefono}
          onChange={handleFilterChange}
          placeholder="Buscar por tel√©fono..."
        />
      </div>
    </section>
  )

  const renderTable = () => (
    <div className="table-container">
      <div className="table-wrapper">
        <table className="data-table" role="table">
          <thead className="table-header">
            <tr>
              <th className="table-header-cell" scope="col">ID</th>
              <th className="table-header-cell" scope="col">Usuario</th>
              <th className="table-header-cell" scope="col">Nombre</th>
              <th className="table-header-cell" scope="col">Apellido</th>
              <th className="table-header-cell" scope="col">Email</th>
              <th className="table-header-cell" scope="col">Tel√©fono</th>
              <th className="table-header-cell" scope="col">Ciudad</th>
              <th className="table-header-cell" scope="col">Direcci√≥n</th>
              <th className="table-header-cell" scope="col">Especialidad</th>
              <th className="table-header-cell" scope="col">Instagram</th>
              <th className="table-header-cell" scope="col">Facebook</th>
              <th className="table-header-cell" scope="col">X (Twitter)</th>
            </tr>
          </thead>
          <tbody>
            {paginatedData.map((user) => (
              <tr key={user.id} className="table-row">
                <TableCell>{user.id}</TableCell>
                <TableCell title={user.usuario}>{user.usuario}</TableCell>
                <TableCell title={user.nombre}>{user.nombre}</TableCell>
                <TableCell title={user.apellido}>{user.apellido}</TableCell>
                <TableCell title={user.email}>{user.email}</TableCell>
                <TableCell title={user.telefono}>{user.telefono}</TableCell>
                <TableCell title={user.ciudad}>{user.ciudad}</TableCell>
                <TableCell title={user.direccion}>{user.direccion}</TableCell>
                <TableCell title={user.especialidad}>{user.especialidad}</TableCell>
                <TableCell title={user.instagram}>{user.instagram}</TableCell>
                <TableCell title={user.facebook}>{user.facebook}</TableCell>
                <TableCell title={user.x}>{user.x}</TableCell>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      {totalPages > 1 && (
        <Pagination
          currentPage={currentPage}
          totalPages={totalPages}
          totalItems={totalItems}
          itemsPerPage={ITEMS_PER_PAGE}
          onPageChange={goToPage}
          hasNextPage={hasNextPage}
          hasPrevPage={hasPrevPage}
          onNextPage={goToNextPage}
          onPrevPage={goToPrevPage}
          onFirstPage={goToFirstPage}
          onLastPage={goToLastPage}
        />
      )}
    </div>
  )

  const renderEmptyState = () => (
    <div className="empty-state">
      <div className="empty-icon" aria-hidden="true">üìã</div>
      <h3 className="empty-title">No hay usuarios</h3>
      <p className="empty-description">
        {hasActiveFilters
          ? 'No se encontraron usuarios que coincidan con los filtros aplicados.'
          : 'No hay usuarios registrados en el sistema.'}
      </p>
    </div>
  )

  const renderError = () => {
    const isCorsError = error && error.includes('CORS')

    return (
      <div className="error-alert" role="alert">
        <span aria-hidden="true">‚ö†Ô∏è</span>
        <div>
          <strong>{isCorsError ? 'Error de Configuraci√≥n CORS' : 'Error de Conexi√≥n'}</strong>
          <p>{error}</p>
          {isCorsError && (
            <div style={{ marginTop: '1rem', padding: '1rem', backgroundColor: '#fff3cd', borderRadius: '4px' }}>
              <h4>Soluci√≥n para el administrador del API:</h4>
              <p>Agregar a la configuraci√≥n CORS del servidor:</p>
              <code style={{ backgroundColor: '#f8f9fa', padding: '0.5rem', borderRadius: '2px' }}>
                Access-Control-Allow-Origin: https://netproadmin.store
              </code>
            </div>
          )}
        </div>
        {!isCorsError && (
          <button
            onClick={loadUsers}
            className="reload-button"
            type="button"
            style={{ marginLeft: '1rem' }}
          >
            Reintentar
          </button>
        )}
      </div>
    )
  }

  if (loading) {
    return <LoadingSpinner message="Cargando usuarios..." />
  }

  return (
    <div className="listado-container">
      <header className="listado-header">
        <h2 className="listado-title">Lista de Usuarios</h2>
        <button
          onClick={loadUsers}
          disabled={loading}
          className="reload-button"
          type="button"
          aria-label="Recargar lista de usuarios"
        >
          <span aria-hidden="true">üîÑ</span>
          Recargar
        </button>
      </header>

      {error && renderError()}

      {!error && (
        <>
          {renderFilters()}

          {hasActiveFilters && (
            <div className="results-counter">
              Mostrando {filteredData.length} de {users.length} usuarios
            </div>
          )}

          {filteredData.length === 0 ? renderEmptyState() : renderTable()}
        </>
      )}
    </div>
  )
}

export default Listado