import { useState, useMemo } from 'react'

export const useFilter = (data, filterConfig = {}) => {
  const [filters, setFilters] = useState({})

  const filteredData = useMemo(() => {
    if (Object.keys(filters).length === 0) {
      return data
    }

    return data.filter(item => {
      return Object.entries(filters).every(([field, filterValue]) => {
        if (!filterValue || filterValue.trim() === '') {
          return true
        }

        const itemValue = item[field]
        if (itemValue == null) {
          return false
        }

        const itemString = itemValue.toString().toLowerCase()
        const filterString = filterValue.toString().toLowerCase()

        const config = filterConfig[field] || {}

        switch (config.type) {
          case 'exact':
            return itemString === filterString
          case 'number':
            return parseFloat(itemValue) === parseFloat(filterValue)
          case 'contains':
          default:
            return itemString.includes(filterString)
        }
      })
    })
  }, [data, filters, filterConfig])

  const setFilter = (field, value) => {
    setFilters(prev => ({
      ...prev,
      [field]: value
    }))
  }

  const clearFilter = (field) => {
    setFilters(prev => {
      const newFilters = { ...prev }
      delete newFilters[field]
      return newFilters
    })
  }

  const clearAllFilters = () => {
    setFilters({})
  }

  const hasActiveFilters = Object.keys(filters).some(key =>
    filters[key] && filters[key].toString().trim() !== ''
  )

  return {
    filteredData,
    filters,
    setFilter,
    clearFilter,
    clearAllFilters,
    hasActiveFilters
  }
}