import { memo } from 'react'

const Pagination = memo(({
  currentPage,
  totalPages,
  totalItems,
  itemsPerPage,
  onPageChange,
  hasNextPage,
  hasPrevPage,
  onNextPage,
  onPrevPage,
  onFirstPage,
  onLastPage
}) => {
  if (totalPages <= 1) return null

  const startItem = (currentPage - 1) * itemsPerPage + 1
  const endItem = Math.min(currentPage * itemsPerPage, totalItems)

  const renderPageNumbers = () => {
    const pages = []
    const maxVisiblePages = 5

    let startPage = Math.max(1, currentPage - Math.floor(maxVisiblePages / 2))
    let endPage = Math.min(totalPages, startPage + maxVisiblePages - 1)

    if (endPage - startPage + 1 < maxVisiblePages) {
      startPage = Math.max(1, endPage - maxVisiblePages + 1)
    }

    if (startPage > 1) {
      pages.push(
        <button
          key={1}
          onClick={() => onPageChange(1)}
          className="pagination-button"
          aria-label="Ir a la p√°gina 1"
        >
          1
        </button>
      )
      if (startPage > 2) {
        pages.push(
          <span key="ellipsis-start" className="pagination-ellipsis">
            ...
          </span>
        )
      }
    }

    for (let i = startPage; i <= endPage; i++) {
      pages.push(
        <button
          key={i}
          onClick={() => onPageChange(i)}
          className={`pagination-button ${i === currentPage ? 'active' : ''}`}
          aria-label={`Ir a la p√°gina ${i}`}
          aria-current={i === currentPage ? 'page' : undefined}
        >
          {i}
        </button>
      )
    }

    if (endPage < totalPages) {
      if (endPage < totalPages - 1) {
        pages.push(
          <span key="ellipsis-end" className="pagination-ellipsis">
            ...
          </span>
        )
      }
      pages.push(
        <button
          key={totalPages}
          onClick={() => onPageChange(totalPages)}
          className="pagination-button"
          aria-label={`Ir a la p√°gina ${totalPages}`}
        >
          {totalPages}
        </button>
      )
    }

    return pages
  }

  return (
    <div className="pagination-container" role="navigation" aria-label="Paginaci√≥n">
      <div className="pagination-info">
        Mostrando {startItem} - {endItem} de {totalItems} resultados
      </div>

      <div className="pagination-controls">
        <button
          onClick={onFirstPage}
          disabled={!hasPrevPage}
          className="pagination-button"
          aria-label="Ir a la primera p√°gina"
        >
          ‚èÆÔ∏è Primera
        </button>

        <button
          onClick={onPrevPage}
          disabled={!hasPrevPage}
          className="pagination-button"
          aria-label="Ir a la p√°gina anterior"
        >
          ‚¨ÖÔ∏è Anterior
        </button>

        {renderPageNumbers()}

        <button
          onClick={onNextPage}
          disabled={!hasNextPage}
          className="pagination-button"
          aria-label="Ir a la p√°gina siguiente"
        >
          Siguiente ‚û°Ô∏è
        </button>

        <button
          onClick={onLastPage}
          disabled={!hasNextPage}
          className="pagination-button"
          aria-label="Ir a la √∫ltima p√°gina"
        >
          √öltima ‚è≠Ô∏è
        </button>
      </div>
    </div>
  )
})

Pagination.displayName = 'Pagination'

export default Pagination